.no-scroll {
  overflow: hidden;
}

.visually-hidden:not(:focus):not(:active) {
  position: absolute !important;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  white-space: nowrap;
}
.visually-hidden:focus, .visually-hidden:active {
  z-index: var(--z-index--visually-hidden);
}