.search {
    margin-left: auto;

    $this: &;

    &__inner{
        display: flex;
        align-items: center;
    }



    &__text{
    }

    &__text_title{
        margin-bottom: 0%;
        margin-top: 0%;
    }

    &__input_wrapper{
        margin-left:10px;
    }

    .relative{
        position: relative;
    }

    &__input {
        border: none;
        border-radius: 300px; 
        width: 40px;        
        height: 40px;       
        transition: all 0.3s ease; 
        box-sizing: border-box; 
        outline: none; 
        box-shadow: none;
        display: block;
        cursor: pointer;
        caret-color: transparent; 
        color: transparent
    }


    &__input_wrapper::after{
        background-image: url('/wp-content/themes/reach/_src/images/search.svg');
        background-size: 20px;
        background-repeat: no-repeat;
        background-position: center;
        content: ''; 
        width: 30px;
        height: 30px;
        position: absolute;
        top:5px;
        left: 15px;
        cursor: pointer;
        pointer-events: none; 
    }
    
    &__input--active {
        width: 300px;        
        height: 40px;       
        padding: 0px 40px;     
        caret-color: #000; 
        color: #000;
    }



}

